import { Component, OnInit } from '@angular/core';
import { OfSchemaModel } from '../../models';
import { FormGroup } from '@angular/forms';
import { NzDrawerService, NzModalService } from 'ng-zorro-antd';
import { OfSelectAdvancedSearchModel } from './of-select-advanced-search.model';

@Component({
  selector: 'of-select-advanced-search',
  templateUrl: './of-select-advanced-search.component.html'
})
export class OfSelectAdvancedSearchComponent implements OnInit {
  schemaModel: OfSchemaModel;
  field: OfSelectAdvancedSearchModel<any>;
  group: FormGroup;

  constructor(private drawerService: NzDrawerService,
              private modalService: NzModalService) {
  }

  ngOnInit(): void {
  }

  onClickAdvancedBtn() {
    if (this.field.showComponentType === 'drawer') {
      this.openDrawer();
      return;
    }
    if (this.field.showComponentType === 'modal') {
      this.openModal();
      return;
    }
  }

  openDrawer() {
    const drawerRef = this.drawerService.create({
      nzTitle: 'Tìm kiếm nâng cao',
      nzContent: this.field.componentAdvanced,
      nzPlacement: 'bottom',
      nzMaskClosable: false,
      nzHeight: '68vh',
      ...this.field.nzDrawerOptions
    });
    drawerRef.afterClose.subscribe(data => {
      this.group.get(this.field.dataField).patchValue(data);
    });
  }

  openModal() {
    const modalRef = this.modalService.create({
      nzTitle: 'Tìm kiếm nâng cao',
      nzContent: this.field.componentAdvanced,
      nzMaskClosable: false,
      ...this.field.nzModalConfig,
    });
    modalRef.afterClose.subscribe(data => {
      this.group.get(this.field.dataField).patchValue(data);
    });
  }
}
